.overlay{
    top: 0;
    left: 0;
    position: absolute;
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);;
    display: none;
    z-index: 2;
}
.overlay.active{
    display: block;
}
.drawer{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 450px;
    background-color: 
    rgba(255, 255, 255, 1);
    box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.2);
    padding: 30px;
}
.title{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}
.products{
    overflow-y: auto;
    height: 70%;
}
.products::-webkit-scrollbar {
    width: 3px;
}
.products::-webkit-scrollbar-track {
    background-color: rgb(217, 217, 217);
    border-radius: 60px;
}
.products::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 6px 
    rgba(157, 212, 88, 1);
    border-radius: 60px;
}
.products>div{
    margin-top: 20px;
}

.buyBlock{
    div{
        display: flex;
        align-items: center;
        width: 100%;
        justify-content: space-between;
        margin-top: 15px;
        div{
            width: 57%;
            height: 10px;
            border-bottom: 1px dashed rgba(223, 223, 223, 1);
        }
    }
    button{
        width: 100%;
        height: 55px;
        background-color: 
        rgba(157, 212, 88, 1);
        border-radius: 18px;
        border: none;
        font-size: 16px;
        font-weight: 600;
        color: #fff;
        position: relative;
        margin-top: 15px;
        img{
            position: absolute;
            right: 35px;
            top: 50%;
            transform: translateY(-50%);
        }
    }
}
.noneItems{
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    h3{
        font-size: 22px;
        margin-top: 15px;
    }
    span{
        margin-top: 5px;
        color: rgba(189, 189, 189, 1);
    }
}
.purchaseComplete{
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    h3{
        font-size: 22px;
        margin-top: 15px;
        color: 
        rgba(135, 194, 10, 1);
    }
    span{
        margin-top: 5px;
        color: rgba(189, 189, 189, 1);
    }
}


@media (max-width: 500px) {
    .overlay{
        display: none;
    }
}