.section1{
    width: 100%;
    padding: 50px 0;
}
.container{
    max-width: 1200px;
    height: 100%;
    margin: 0 auto;
    padding: 0 10px;
}
.block{
    height: 100%;
    width: 100%;
    height: 370px;
    border-radius: 20px;
    background: url('../../../images//section1.jpg') no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 80px;
    img{
        border-radius: 20px;
        height: 50px;
        position: absolute;
        left: 20px;
        top: 15px;
    }
    h2{
        font-size: 38px;
        color: rgba(139, 180, 60, 1);
        span{
            color: 
            rgba(0, 0, 0, 1);
        }
    }
    b{
        font-size: 38px;
        color: 
        rgba(0, 0, 0, 1);
    }
    button{
        width: 180px;
        height: 48px;
        border-radius: 110px;
        background-color: 
        rgba(165, 211, 100, 1);
        border: none;
        font-weight: 600;
        font-size: 18px;
        color: #fff;
        margin-top: 25px;
        cursor: pointer;
        transition: all 0.2s;

        &:hover{
            box-shadow: 0 2px 10px rgba(165, 211, 100, 0.5);
        }
    }
}
@media (max-width: 500px) {
    .block{
        padding-left: 30px
    }
}