.section2{
    width: 100%;
}
.container{
    max-width: 1200px;
    height: 100%;
    margin: 0 auto;
    padding: 0 10px;
}
.section_title{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    h1{
        font-size: 32px;
        display: flex;
        align-items: center;
    }
}
.filter_input{
    display: flex;
    align-items: center;
    div{
        height: 45px;
        width: 45px;
        border-radius: 10px 0 0 10px;
        border: 1px solid rgba(243, 243, 243, 1);
        border-right: none;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        img{
            width: 40%;
        }
    }
    input{
        height: 45px;
        outline: none;
        border-radius: 0 10px 10px 0;
        border: 1px solid rgba(243, 243, 243, 1);
        border-left: none;
        &::placeholder{
            color: 
            rgba(196, 196, 196, 1);
        }
    }
}

.cards{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding-bottom: 30px;
}
.productBlock{
    display: flex;
    justify-content: center;
}
.productBlock>div{
    margin-top: 40px;
}

.noneItems{

}
@media (max-width: 1160px) {
    .cards{
        grid-template-columns: repeat(4, 1fr);
    }
}
@media (max-width: 940px) {
    .cards{
        grid-template-columns: repeat(3, 1fr);
    }
}
@media (max-width: 720px) {
    .cards{
        grid-template-columns: repeat(2, 1fr);
    }
}
@media (max-width: 600px) {
    .section_title{
        flex-direction: column;
        align-items: start;
    }
    .section_title>div{
        margin-top: 10px;
        width: 100%;
        input{
            width: calc(100% - 45px);
        }
    }
}
@media (max-width: 500px) {
    .cards{
        grid-template-columns: repeat(1, 1fr);
    }
    .productBlock>div{
        width: 100%;
    }
}
