.card{
    width: 100%;
    border: 1px solid 
    rgba(243, 243, 243, 1);
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}
.card>img{
    width: 70px;
    height: 70px;
}
.cardInfo{
    display: flex;
    flex-direction: column;
    width: 60%;
    span{
        font-size: 14px;
    }
    b{
        font-size: 14px;
        margin-top: 5px;
    }
}