.header{
    height: 128px;
    width: 100%;
    border-bottom: 1px solid 
    rgba(234, 234, 234, 1);
}
.container{
    max-width: 1200px;
    height: 100%;
    margin: 0 auto;
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    a{
        text-decoration: none;
        display: flex;
        color: #000;
        div{
            margin-left: 15px
        }
        div p{
            color: 
            rgba(157, 157, 157, 1);
        }
    }
}


.nav_list{
    display: flex;
    div{
        cursor: pointer;
        display: flex;
        align-items: center;
        margin-left: 30px;
        text-decoration: none;
        b{
            color: rgba(92, 92, 92, 1);
            margin-left: 10px;
        }
    }
    a{
        margin-left: 30px;
    }
}
.burger{
    display: none;
}
.burgerList{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    height: 100vh;
    width: 100%;
    background-color: #fff;
    transform: translateY(-100vh);
    transition: 0.8s all;
}
.burgerList.active{
    transform: translateY(0vh);
}
.closeBurger{
    display: flex;
    justify-content: end;
    padding: 30px 30px 0 0;
    margin-bottom: 15px;
}
.burgerElements{
    border-bottom: 1px solid rgba(157, 157, 157, 0.423);
    padding: 10px 10px;
    a{
        text-decoration: none;
        color: #000;
        display: flex;
        align-items: center;
        span{
            margin-left: 15px;
            color: rgb(113, 113, 113);
        }
        img{
            width: 25px;
        }
    }
    div{
        cursor: pointer;
        display: flex;
        align-items: center;
        margin-right: 30px;
        text-decoration: none;
        span{
            margin-left: 15px;
            color: rgb(113, 113, 113);
        }
        img{
            width: 25px;
        }
    }
}
@media (max-width: 720px) {
    .burger{
        display: flex;
    }
    .nav_list{  
        display: none;
    }
}