.card{
    width: 220px;
    border: 1px solid rgba(243, 243, 243, 1);
    border-radius: 40px;
    transition: all 0.2s;   
    cursor: pointer;
    position: relative;
    padding: 0 30px 30px 30px;
    &:hover{
        transform: translateY(-10px);
        box-shadow: 0 14px 30px rgba(0, 0, 0, 0.05);
    }
    img{
        width: 100%;
        border-radius: 20px;
    }
    span{
        font-size: 14px;
    }

    button{
        cursor: pointer;
        height: 32px;
        width: 32px;
        background: rgba(255, 255, 255, 1); 
        border: 1px solid rgba(242, 242, 242, 1);
        border-radius: 8px;
        img{
            width: 40%;
        }
    }
    button.active{
       background: linear-gradient(180deg, rgba(137, 240, 156, 1), rgba(60, 199, 85, 1));
    }
}
.price{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    div{
        display: flex;
        flex-direction: column; 
    }
    b{
        font-size: 15px;
        margin-top: 5px;
    }
    span{
        color: rgba(189, 189, 189, 1);
        font-size: 16px;
    }
}
.like{
    border: 1px solid rgba(248, 248, 248, 1);
    color: 
    rgb(255, 255, 255);
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 7px;
    position: absolute;
    left: 25px;
    top: 25px;
    transition: 0.1s all;
    &:hover{
        
        opacity: 0.8;
    }
}
.like.active{
    border: 1px solid rgb(255, 255, 255);
    background-color: rgba(254, 240, 240, 1);
}
