.container{
    max-width: 1200px;
    height: 100%;
    padding: 0 10px;
    margin: 0 auto;
}
.title{
    display: flex;
    align-items: center;
    
    
    padding-top: 40px;
    button{
        height: 35px;
        width: 35px;
        border: 1px solid rgba(242, 242, 242, 1);
        border-radius: 8px;
        background-color: #fff;
        cursor: pointer;
    }
    h1{
        margin-left: 15px;
    }
}
.products{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding-bottom: 30px;
}
.products>div{
    display: flex;
    justify-content: center;
    margin-top: 40px;
}
.noneItem{
    height: 60vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    h2{
        margin-top: 15px;
    }
    span{
        margin-top: 5px;
        color: rgba(157, 157, 157, 1);
    }
}


@media (max-width: 1160px) {
    .products{
        grid-template-columns: repeat(4, 1fr);
    }
}
@media (max-width: 930px) {
    .products{
        grid-template-columns: repeat(3, 1fr);
    }
}
@media (max-width: 710px) {
    .products{
        grid-template-columns: repeat(2, 1fr);
    }
}
@media (max-width: 480px) {
    .products{
        grid-template-columns: repeat(1, 1fr);
    }
    .products>div{
        width: 100%;
    }
    .products>div>div{
        width: 100%;
    }
}