.wrapper{
    width: 100%;
}
@media (max-width: 500px) {
    .wrapper>div:nth-child(1)>div{
        width: 400px;
    }
    .wrapper>div:nth-child(1)>div>div:nth-child(3) div div{
        width: 48%;
    }
    .wrapper>div:nth-child(1)>div>div:nth-child(3) div b{
        text-align: end;
    }
}
@media (max-width: 410px) {
    .wrapper>div:nth-child(1)>div{
        width: 350px;
    }
    .wrapper>div:nth-child(1)>div>div:nth-child(3) div div{
        width: 40%;
    }
    .wrapper>div:nth-child(1)>div>div:nth-child(2)>div{
        padding: 10px;
    }
}
@media (max-width: 370px) {
    .wrapper>div:nth-child(1)>div{
        width: 100%;
    }
}
